<template>
  <div class="screenInfo">
    <!-- 标题 -->
    <van-nav-bar title="筛选结果" :fixed="true" left-text="返回" left-arrow @click-left="onClickLeft" />
    <div class="carlistBox">
      <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
        <div
          class="carItem"
          v-for="(item, index) in list"
          :key="index"
          @click="toCarInfo(item.SerialID)"
        >
          <van-image :src="item.Picture" width="150" height="100">
            <template v-slot:loading>
              <van-loading type="spinner" size="20" />
            </template>
          </van-image>
          <p class="name">{{item.Name}}</p>
          <p class="price">{{item.DealerPrice}}</p>
        </div>
      </van-list>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      info: {},
      index: 1,
      list: [],
      loading: false,
      finished: false
    };
  },
  activated(){    //每次进入重新获取数据
    this.info = JSON.parse(localStorage.getItem("info")) || {}
    this.index = 1
    this.loading =true
    this.finished=false
    this.list=[]
    if(this.loading){     
      this.onLoad()
    }
  },
  
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    toCarInfo(id) {
      this.$router.push({ path: "/carinfo", query: { SerialID: id } });
    },
    onLoad() {
      // 异步更新数据
      let _self = this;
      setTimeout(() => {
        axios
          .get("https://wxs.app.yiche.com/carselect.ashx", {
            params: {
              p: _self.info.p,
              l: _self.info.l,
              c: _self.info.c,
              g: _self.info.g,
              page: _self.index,
              s: 4
            }
          })
          .then(function(res) {
            // window.console.log(_self.info);
            _self.list.push(...res.data.Data.List);
            _self.index++;
          });

        //加载状态结束
        this.loading = false;

        // 数据全部加载完成
        if (this.list.length >= this.info.count) {
          this.finished = true;
        }
      }, 500);
    }
  }
};
</script>
<style lang="scss">
.screenInfo {
  padding: 50px 0 20px;
  .van-nav-bar {
    height: 50px;
    line-height: 50px;
    background: #5f93ff;
    color: #fff;
    .van-nav-bar__left {
      .van-nav-bar__text,
      i {
        color: #fff;
      }
    }
    .van-nav-bar__title {
      color: #fff;
    }
  }
  .carlistBox {
    .van-list {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      .van-list__finished-text,
      .van-list__loading {
        width: 100%;
        text-align: center;
      }
    }
    .carItem {
      width: 50%;
      text-align: center;
      margin: 10px 0;
      .name {
        font-size: 16px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
      .price {
        font-size: 12px;
        color: #f3726e;
        margin-top: 5px;
      }
    }
  }
}
</style>